.example-enter {
  opacity: 0.01;
  -webkit-transform: translate(100px);
      -ms-transform: translate(100px);
          transform: translate(100px);
}

.example-enter.example-enter-active {
  opacity: 1;
  -webkit-transform: translateX(0);
      -ms-transform: translateX(0);
          transform: translateX(0);
  -webkit-transition: all .1s ease-out;
  -o-transition: all .1s ease-out;
  transition: all .1s ease-out;
}

.example-leave {
  opacity: 1;
  -webkit-transform: translateX(0);
      -ms-transform: translateX(0);
          transform: translateX(0);
}

.example-leave.example-leave-active {
  opacity: 0.01;
  -webkit-transition: all .1s ease-out;
  -o-transition: all .1s ease-out;
  transition: all .1s ease-out;
  -webkit-transform: translateX(-100px);
      -ms-transform: translateX(-100px);
          transform: translateX(-100px);
}

.headline-enter {
  opacity: 0.01;
  -webkit-transform: translate(150px);
      -ms-transform: translate(150px);
          transform: translate(150px);
}

.headline-enter.headline-enter-active {
  opacity: 1;
  -webkit-transform: translateX(0px);
      -ms-transform: translateX(0px);
          transform: translateX(0px);
  -webkit-transition: all .25s ease-in-out;
  -o-transition: all .25s ease-in-out;
  transition: all .25s ease-in-out;
}

.headline-leave {
  opacity: 1;
}

.headline-leave.headline-leave-active {
  -webkit-transform: translate(-150px, 0);
      -ms-transform: translate(-150px, 0);
          transform: translate(-150px, 0);
  opacity: 0.01;
  -webkit-transition: all .25s ease-out;
  -o-transition: all .25s ease-out;
  transition: all .25s ease-out;
}

/* where would you appaer? */

.headline-appear {
  opacity: 0.01;
  -webkit-transform: translate(0, -50px);
      -ms-transform: translate(0, -50px);
          transform: translate(0, -50px);
}

.headline-appear.headline-appear-active {
  opacity: 1;
  -webkit-transform: translate(0, 0px);
      -ms-transform: translate(0, 0px);
          transform: translate(0, 0px);
  -webkit-transition: opacity .6s ease-in-out, -webkit-transform .2s ease-in;
  transition: opacity .6s ease-in-out, -webkit-transform .2s ease-in;
  -o-transition: transform .2s ease-in, opacity .6s ease-in-out;
  transition: transform .2s ease-in, opacity .6s ease-in-out;
  transition: transform .2s ease-in, opacity .6s ease-in-out, -webkit-transform .2s ease-in;
}



